.NavBar_layout{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 2rem;
    width: 100%;
}

.NavBar_logo_layout{
    width: 96px;
height: 18.75px;
display: flex;
}

.NavBar_input_layout{
display: flex;

height: 55px;
justify-content: center;
align-items: stretch;
}


.NavBar_input_layout button{
font-family: 'Muli';
font-size: 14px;
line-height: 16px;
color: #333333;
background: #FFFFFF;
border: none;
padding: 1rem;
box-shadow: 0px 1px 6px rgba(0, 0, 0, 0.1);
}


@media  screen and (max-width: 650px){
    .NavBar_layout{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
    }

    .NavBar_input_layout  {
        margin-top: 1rem;
    }
    .NavBar_logo_layout{
         
        margin-top: 1rem;
    
  }}