.input_layout{
    background-color:rgba(79, 79, 79, 0.4) ;
    margin: 0%;
    padding: 0%;
    width: 100%;
    position: absolute;
    height: 100%;
    z-index: 5;    
}
.input_layout_body{
    height: 460px;
    background-color: #FFFFFF;
}
.input_layout_button{
    padding: 3rem;
    display: flex;;
    align-items: flex-start;
    justify-content: center;

}
.input_layout_button button{
    flex-grow: 1;
    margin: 1rem;
    background: #FFFFFF;
    box-shadow: 0px 1px 6px rgba(0, 0, 0, 0.1);
}


@media  screen and (max-width: 650px){
.input_layout_button {
    flex-direction: column;
}
    .input_layout_body{
        height: auto;
    }
  }