.Location_Layout{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    width:100%;
}
.Location_Layout button{
width:100%;
filter: drop-shadow(0px 1px 6px rgba(0, 0, 0, 0.1));
border:none;
border-top-left-radius: 16px;
border-bottom-left-radius: 16px;
}
.Location_Layout button p:first-child{
font-family: 'Muli';
font-style: normal;
font-weight: 800;
font-size: 9px;
line-height: 11px;
text-transform: uppercase;
color: #333333;
display: flex;
align-items: flex-start;
    }
.Location_Layout button p:last-child{
font-family: 'Muli';
font-style: normal;
font-weight: 400;
font-size: 14px;
line-height: 18px;
color: #333333;
display: flex;
align-items: flex-start;
}
.location__data{
    /* background: red; */
    width:100%;
}

li{
    width:100%;
    display: flex;
    justify-content:flex-start;
    align-items: baseline;
    padding: 1rem;
}

@media  screen and (max-width: 650px){

    .Location_Layout button{
        border-radius: 16px;
    }
}