.App {
padding-top: 0.5rem;
position: relative;
}

.content{
  display: grid;
  grid-gap: 1.5rem;
  grid-template-columns: repeat(3, 1fr);
}
li{
  list-style: none;
}
#createdby{
font-family: 'Montserrat';
font-style: normal;
font-weight: 500;
font-size: 14px;
line-height: 17px;
text-align: center;
color: #828282;
}
.contentdata{
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.contentdata h1{ font-family: 'Montserrat';
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 29px;
  color: #333333;
  }
.contentdata p{
  font-family: 'Montserrat';
font-style: normal;
font-weight: 500;
font-size: 14px;
line-height: 17px;
color: #4F4F4F;
}
@media  screen and (max-width: 650px){
  .content{
    display:flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
  }
  .contentdata h1 {
    font-size: 16px;
  }
}

@media  screen and (max-width: 1040px){
  .content{
    grid-template-columns: repeat(2, 1fr);
  }
}