.Guest_Layout{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    width:100%;
}
.Guest_Layout button{
width:100%;
border: 1px solid #333333;
filter: drop-shadow(0px 1px 6px rgba(0, 0, 0, 0.1));
border:none;
}
.Guest_Layout button p:first-child{
    font-family: 'Muli';
    font-style: normal;
    font-weight: 800;
    font-size: 9px;
    line-height: 11px;
    text-transform: uppercase;
    color: #333333;
    display: flex;
    align-items: flex-start;
    }
.Guest_Layout button p:last-child{
    font-family: 'Muli';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: #333333;
    display: flex;
    align-items: flex-start;
}
.guest__data label{
    /* width: 114px; */
    width:100%;
    font-family: 'Muli';
font-style: normal;
font-weight: 700;
font-size: 14px;
line-height: 18px;
color: #333333;

}
.guest__data label p:first-child{
    font-family: 'Muli';
font-style: normal;
font-weight: 700;
font-size: 14px;
line-height: 18px;
color: #333333;
text-transform: none;
}
.guest__data label p:last-child{
    font-family: 'Muli';
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 18px;
    color: #BDBDBD;
    text-transform: none;
}

.buttonslayout{
    display: flex;
    flex-direction: row;
    align-items: baseline;
    justify-content: space-between;
    width: fit-content;
    padding: 0%;
}
.buttonslayout button{
    box-sizing: border-box;
    width: 23px;
    border: 1px solid #828282;
    border-radius: 4px;
    margin-left: 0%;
}
.Guestdrop{
    width:100%;
}
.buttonslayout p{
    font-family: 'Muli';
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 18px;
    color: #333333;
    width: 9px;
height: 18px;
margin-right: 1rem;
}
@media  screen and (max-width: 650px){

    #guestbutton{
        border-radius: 16px;
    }
}