.card__layout{
    display: flex;
    flex-direction: column;
}

.card__layout__img img{
    width: 100%;
    border-radius: 24px;
    height: 269px;
}
.card__layout__info{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.card__layout__info-superhost{
width: 100px;
height: 28px;
border: 1px solid #4F4F4F;
border-radius: 12px;
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
}
.card__layout__info-superhost p{
font-family: 'Montserrat';
font-style: normal;
font-weight: 700;
font-size: 12px;
line-height: 15px;
text-transform: uppercase;
color: #4F4F4F;
}
.card__layout__info__title{
font-family: 'Montserrat';
font-style: normal;
font-weight: 500;
font-size: 14px;
line-height: 17px;
color: #828282;
}

.card__layout__info__title p{
width: 32px;
height: 17px;
font-family: 'Montserrat';
font-style: normal;
font-weight: 500;
font-size: 14px;
line-height: 17px;
color: #4F4F4F;
}
.card__layout__info__rating{
display: flex;
align-items: center;
justify-content: space-between;
}
.card__layout__info__rating p{
width: 30px;
height: 17px;
font-family: 'Montserrat';
font-style: normal;
font-weight: 500;
font-size: 14px;
line-height: 17px;
color: #4F4F4F;
margin-left: 7px;
}

.card__layout-title{
display : flex;
justify-content: flex-start;
align-items: center;
}

.card__layout-title h2{
height: 20px;
font-family: 'Montserrat';
font-style: normal;
font-weight: 600;
font-size: 16px;
line-height: 20px;
text-align: right;
color: #333333;
}

@media  screen and (max-width: 650px){
    
    .card__layout-title h2 {
        text-align: center;
        height: auto;
    }
}